import {Component, OnInit, ViewChild} from '@angular/core';
import {TodoModel, TodoService} from '../shared';
import {NgForm} from '@angular/forms';
import {Router} from '@angular/router';

@Component({
  selector: 'app-create-todo',
  templateUrl: './create-todo.component.html',
  styleUrls: ['./create-todo.component.css']
})
export class CreateTodoComponent implements OnInit {
  private service: TodoService;

  @ViewChild('formTodo', { static: true }) formTodo: NgForm;
  public todo: TodoModel;

  constructor(service: TodoService, private route: Router) {
    this.service = service;
  }

  ngOnInit(): void {
    this.todo = new TodoModel(new Date().getTime());
  }

  create(): void {
    if (this.formTodo.form.valid) {
      this.service.create(this.todo);
      this.route.navigate(['/todos']).then(r => console.log('foi'));
    }
  }
}
