import {Injectable} from '@angular/core';

import {TodoModel} from '../todo.model';

@Injectable({
  providedIn: 'root'
})
export class TodoService {

  constructor() { }

  listAll(): TodoModel[] {
    const todos: string = localStorage.todos;
    return !!todos ? JSON.parse(todos) : [];
  }

  create(todo: TodoModel): void {
   const todos: TodoModel[] = this.listAll();
   todo.id = new Date().getTime();
   todos.push(todo);
   localStorage.todos = JSON.stringify(todos);
  }

  findById(id: number): TodoModel {
    return this.listAll().find((todoModel: TodoModel) =>  todoModel.id === id);
  }

  updateTodo(todo: TodoModel): void {
    const todoModels = this.listAll();
    todoModels.forEach((todoModel, i, todos) => {
      if (todoModel.id === todo.id) { todos[i] = todo; }
    });
    localStorage.todos =  JSON.stringify(todoModels);
  }

  remove(id: number): void {
    localStorage.todos = JSON.stringify(this.listAll().filter(todoModel => todoModel.id !== id));
  }

  completeTodo(id: number): void {
    const todoModels = this.listAll();
    todoModels.forEach((todo, i, todos) => {
      if (todo.id === id) {
        todo.complete = true;
      }
    });
    localStorage.todos = JSON.stringify(todoModels);
  }
}
