import {Component, OnInit, ViewChild} from '@angular/core';
import {TodoModel, TodoService} from '../shared';
import {ActivatedRoute, Router} from '@angular/router';
import {NgForm} from '@angular/forms';

@Component({
  selector: 'app-edit-todo',
  templateUrl: './edit-todo.component.html',
  styleUrls: ['./edit-todo.component.css']
})
export class EditTodoComponent implements OnInit {
  @ViewChild('formTodo', { static: true }) formTodo: NgForm;
  public todo: TodoModel;

  constructor(private service: TodoService, private router: Router, private route: ActivatedRoute) { }

  ngOnInit(): void {
    const id = +this.route.snapshot.params.id;
    this.todo = this.service.findById(id);
  }

  update(): void {
    if (this.formTodo.valid) {
      this.service.updateTodo(this.todo);
      this.router.navigate(['/todos']).then(r => console.log('foi'));
    }
  }
}
