import { Component, OnInit } from '@angular/core';
import {TodoModel, TodoService} from '../shared';
import {Router} from '@angular/router';

@Component({
  selector: 'app-list-todos',
  templateUrl: './list-todos.component.html',
  styleUrls: ['./list-todos.component.css']
})
export class ListTodosComponent implements OnInit {
  public todos: TodoModel[];

  constructor(private service: TodoService,  private route: Router) {
  }

  ngOnInit(): void {
    this.todos = this.service.listAll();
    this.todos = [
      new TodoModel(1, 'dasddasa', false)
    ];
  }

  listAll(): TodoModel[] {
    return this.service.listAll();
  }

  remove($event: any, id: number): void {
    $event.preventDefault();
    this.service.remove(id);
  }

  edit(todo: TodoModel): void {
    this.service.updateTodo(todo);
  }

  completeTodo(id: number): void {
    this.service.completeTodo(id);
  }

  findById(id: number): TodoModel {
    return this.service.findById(id);
  }

  create(): void {
    this.route.navigate(['/create-todo/create-todo']).then(r => console.log('foi'));
  }
}
